/* You can add global styles to this file, and also import other style files */
/* @import "~bootstrap/scss/bootstrap"; */
@import "~@fortawesome/fontawesome-free/scss/fontawesome";
@import "~@fortawesome/fontawesome-free/scss/brands";
@import "~@fortawesome/fontawesome-free/scss/regular";
@import "~@fortawesome/fontawesome-free/scss/solid";
@import "assets/kendo-theme/custom-kendo-theme";
@import url("https://fonts.googleapis.com/css?family=Roboto:400,100,100italic,300,300ita‌​lic,400italic,500,500italic,700,700italic,900italic,900");
@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";
@import "~ngx-toastr/toastr";
body {
  @apply bg-gray-900;
  font-family: Roboto, sans-serif !important;
}
app-root {
  .text-orange {
    color: #d96b00;
  }
  .disabled {
    pointer-events: none;
    cursor: default;
    opacity: 0.6;
  }
  a.disabled {
    pointer-events: none;
    cursor: default;
    opacity: 0.6;
  }
  .message-wrp {
    .message {
      display: list-item !important;
      margin-top: 10px;
      margin-left: 9em;
      list-style-type: disclosure-closed;
    }
  }
  .p-dropdown:not(.p-disabled):hover,
  .p-inputtext:enabled:hover {
    border-color: #2e2d29 !important;
  }
  .p-inputtext:enabled:focus,
  .p-dropdown:not(.p-disabled):focus {
    border-color: #2e2d29 !important;
    box-shadow: 0 0 0 1px #262523 !important;
  }
  p-autocomplete.ng-dirty.ng-invalid > .p-autocomplete > .p-inputtext {
    border-color: #a7333a !important;
  }
  .btn + .btn {
    margin-left: 0px;

    /* Important part */
    position: relative;
    transition: padding-right 0.3s ease-out;
  }
  .btn.primary {
    border-radius: 7px !important;
    width: 6em;
    filter: drop-shadow(1px 2px 2px #181a1d);
  }
  .btn.loading {
    background-color: #d96b00cc;
    padding-right: 40px;
  }
  .btn.loading:after {
    content: "";
    position: absolute;
    border-radius: 100%;
    right: 6px;
    top: 50%;
    width: 0px;
    height: 0px;
    margin-top: -2px;
    border: 2px solid rgba(255, 255, 255, 0.5);
    border-left-color: #fff;
    border-top-color: #fff;
    animation: spin 0.6s infinite linear, grow 0.3s forwards ease-out;
  }
  @keyframes spin {
    to {
      transform: rotate(359deg);
    }
  }
  @keyframes grow {
    to {
      width: 14px;
      height: 14px;
      margin-top: -8px;
      right: 13px;
    }
  }
  .k-grid-header span.k-link {
    cursor: pointer;
  }
  .form-section {
    margin-top: 1em;
  }
  .form {
    &.portlet-body {
      background: #2e323ade;
      padding: 2em !important;
      border-radius: 21px !important;
      box-shadow: 1px 1px 6px 1px #1e2023;
    }
    .form-body {
      overflow-y: auto;
      max-height: 55em;
      .form-group {
        margin-left: 0.6em;
        .form-control,
        input[type="text"] {
          border: none;
          border-radius: 0.5em !important;
          background-color: #222529 !important;
          height: 37px;
        }
      }
    }
    .form-footer {
      padding-top: 1em;
      border-top: 1px solid #3e424a;
      margin: 0 0.8em;
      padding-left: 0.5em;
      .form-actions {
        margin: 0;
      }
    }
  }
  .k-dropdown-wrap .k-input {
    padding: 5.455px 12px;
    height: auto;
  }
  .area-groups .k-input{
    padding: 5.455px 5px !important;
  }
  [type="text"] {
    border-color: transparent;
    background-color: transparent;
  }
  .table {
    @apply w-full;
    th {
      @apply text-gray-200 text-left text-xs uppercase border-b-2 border-gray-600 py-4;
    }
    td {
      @apply text-sm py-4 text-gray-300 border-b border-gray-700;
    }
  }
  .field {
    @apply my-6 w-full;
    label {
      @apply block text-gray-400 font-bold text-xs mb-1 uppercase;
    }
    input,
    select {
      @apply outline-none bg-gray-700 px-6 py-3 text-gray-200 rounded w-full;
      &:focus {
        @apply shadow;
      }
    }
  }
  .d-block {
    display: block !important;
  }

  /***
  General typography
  ***/

  /* Headings */
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-top: 0;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    small {
      font-size: 16px;
      color: #d96b00;
      opacity: 0.7;
    }
  }
  h1 {
    font-size: 33px;
  }
  h2 {
    font-size: 27px;
  }
  h3 {
    font-size: 24px;
  }
  h4 {
    font-size: 17px;
  }
  h5 {
    font-size: 13px;
  }
  h6 {
    font-size: 12px;
  }
  p {
    color: #fff;
    font-size: 16px;
  }
  .mt-6 {
    margin-top: 2em !important;
  }

  /* Headings helper text */

  /* Disabled Navigation Link */
  .disabled-link .disable-target {
    opacity: 0.5 !important;
    filter: alpha(opacity=50) !important;
  }
  .disabled-link .disable-target:hover {
    cursor: not-allowed !important;
  }
  .disabled-link:hover {
    cursor: not-allowed !important;
  }
  .k-pager-numbers .k-link,
  .k-pager-numbers .k-state-selected {
    display: flex;
  }
  .flex {
    display: flex !important;
  }
}

/*** Form Validation Messages **/
arv-validation-messages {
  .invalid-feedback {
    display: block;
    color: #ad393f;
    list-style-type: disclosure-closed;
    width: 100%;
    p {
      font-size: 12px;
      color: #cd363e;
      margin-bottom: 0;
      margin-top: 0.3em;
    }
  }
}
[type="text"],
[type="time"],
select,
textarea {
  font-size: inherit;
}
.toast-top-center {
  left: 11% !important;
  top: 1% !important;
}
@-moz-document url-prefix() {
.k-calendar-view {
  margin: unset;
}
}